s: center;\n            @include border-radius(35%);\n            background-color: $primary-brand;\n            color: #fff;\n            margin-top: -0.35em;\n            margin-right: 0.2em;\n            position: absolute;\n            text-align: center;            \n            left: 1.4em;            \n            line-height: 1.4;\n            height: 1.4em;\n            font-size: 0.8em;\n            @include breakpoint(phablet) {\n                height: 1.5em;\n                min-width: 2em;                \n                line-height: 1.5;\n                font-size: .8em;\n            }\n            &.hidden {\n                display: none;\n            }\n        }\n        .map-icon {\n            svg {\n                width: 21px;\n                height: 22px;\n                \n                @include breakpoint(phablet) {\n                    width: 26px;\n                    height: 27px;\n                }\n                &:hover {\n                    color: #1073BC;\n                    cursor: pointer;\n                }\n            }\n        }\n    }\n    .map-item-list-wrapper {\n        position: absolute;\n        .map-item-list-container {\n            @include border-radius(0 0 3px 3px);\n            @include transition(all 0.1s ease-in-out);\n            box-sizing: border-box;\n            overflow: hidden;\n            max-height: 0;\n            position: relative;\n            top: 10px;\n            background-color: #fbfcfd;\n            right: 205px;\n            width: 260px;\n            @include breakpoint(phablet) {\n                right: 245px;\n                width: 300px;\n            }\n            @media print {\n                display: none;\n            }\n            &.open {\n                @include box-shadow(\n                    0 5px 5px -3px rgba(0, 0, 0, 0.2),\n                    0 8px 10px 1px rgba(0, 0, 0, 0.14),\n                    0 3px 14px 2px rgba(0, 0, 0, 0.12)\n                );\n      